import React from "react";
import moment from "moment";
import { useFormik } from "formik";
import { object, string } from "yup";

// UI
import { Box, Typography, Paper, Tooltip } from "@material-ui/core";
import UserAvatar from "../../../components/UserAvatar";
import LogStatusHandler from "../LogStatusHandler";

// Constants
import { FULL_DATE_FORMAT, DATE_FORMAT } from "constants/dateFormats";
import { validationErrorMessages, WORK_TYPES } from "constants/developerReport";

// Utils
import { formatTimeDuration, getType } from "utils/managerReportsUtils";
import { getUserAvatar } from "utils/developersUtils";

// Styles
import { StyledTextArea, StyledTypography, StyledColoredName } from "./style";
import IconButton from "@material-ui/core/IconButton";
import EditIcon from "@material-ui/icons/Edit";
import AvTimerIcon from "@material-ui/icons/AvTimer";
import Chip from "../../../components/Chip";

let schema = object().shape({
    descriptions: string().required(),
});

const localFixAvatar = avatar => (avatar ? `/uploads${avatar}` : null);
const getWorkTypeLabel = value =>
    value ? WORK_TYPES.find(item => item.value === value) : {};

function InReviewItems({
    onUpdateLog,
    onEdit,
    onUpdateLogStatus,
    logTypes,
    user_id,
    project_id,
    log_type_id,
    date,
    total,
    descriptions,
    status,
    id,
    from_time,
    to_time,
    disabled,
    onShowHistory,
    work_type,
}) {
    const getLogType = id => {
        return logTypes.find(item => item.id === id)?.type;
    };

    function validate(values) {
        const errors = {};
        if (!values.descriptions) {
            errors.descriptions = validationErrorMessages.descriptions;
        }
        return errors;
    }

    const formik = useFormik({
        initialValues: {
            date,
            descriptions,
            from_time,
            to_time,
            project_id: project_id?.id,
            log_type_id,
            work_type,
        },
        validate,
        validationSchema: schema,
        validateOnChange: false,
        enableReinitialize: true,
        onSubmit: (data, { resetForm }) => {
            if (descriptions === data.descriptions) return;
            onUpdateLog(data, id);
        },
    });

    const onUpdateStatus = async (...param) => {
        const errors = await formik.validateForm();
        if (Object.keys(errors).length) return;

        await formik.submitForm();
        onUpdateLogStatus(...param);
    };

    const workType = getWorkTypeLabel(work_type);

    return (
        <>
            <Box
                position="relative"
                display="flex"
                flexWrap="wrap"
                component={Paper}
                marginBottom="10px"
                variant="outlined"
                py={1}
                px={1}
                borderRadius={8}
            >
                <Box
                    display="flex"
                    flexDirection="column"
                    justifyContent="flex-start"
                    alignItems="center"
                    width={200}
                    overflow="hidden"
                    whiteSpace="nowrap"
                    textOverflow="ellipsis"
                    px={2}
                    mb={2}
                >
                    <UserAvatar
                        size={100}
                        firstName={user_id.firstname}
                        lastName={user_id.lastname}
                        avatar={getUserAvatar(localFixAvatar(user_id.avatar))}
                    />

                    <Box
                        justifyContent="center"
                        alignItems="center"
                        px={2}
                        mb={2}
                        width="100%"
                        display="flex"
                    >
                        <Typography variant="h6" noWrap py={2}>
                            {`${user_id.firstname} ${user_id.lastname}`}
                        </Typography>
                    </Box>
                    <Box>
                        <Chip type={getType(status)} label={status} />
                    </Box>
                </Box>

                <Box
                    display="flex"
                    flexDirection="column"
                    flex={1}
                    px={2}
                    mb={2}
                >
                    <StyledTypography component={"span"} align="left" noWrap>
                        <b>{`Project: `}</b>
                        <StyledColoredName
                            text={project_id.name}
                            fontSize={20}
                            color={project_id.color}
                        />
                    </StyledTypography>
                    <StyledTypography align="left" noWrap>
                        <b>{`Log type: `}</b>
                        {getLogType(log_type_id)}{" "}
                    </StyledTypography>
                    <StyledTypography align="left" noWrap>
                        <b>{`Week: `}</b>
                        {moment(date, FULL_DATE_FORMAT).isoWeek()}
                    </StyledTypography>
                    <StyledTypography align="left" noWrap>
                        <b>{`Day: `}</b>
                        {moment(date, FULL_DATE_FORMAT).format(DATE_FORMAT)}
                    </StyledTypography>
                    <StyledTypography align="left" noWrap>
                        <b>{`Time spent: `}</b>
                        {formatTimeDuration(total)}
                    </StyledTypography>
                    <StyledTypography align="left" noWrap>
                        <b>{`From `}</b>
                        {from_time}
                        <b>{` to `}</b>
                        {to_time}
                    </StyledTypography>
                    {workType?.value && (
                        <StyledTypography
                            align="left"
                            noWrap
                            colortype={workType?.color}
                        >
                            <b>{getWorkTypeLabel(work_type)?.label}</b>
                        </StyledTypography>
                    )}
                </Box>

                <Box
                    flex={4}
                    px={2}
                    display="flex"
                    flexDirection="column"
                    justifyContent={
                        status === "declined" ? "flex-start" : "space-between"
                    }
                    minWidth="200px"
                >
                    <Box
                        position="relative"
                        right="20px"
                        top="0"
                        display="flex"
                        justifyContent="flex-end"
                    >
                        <Tooltip title="Edit" placement="top">
                            <IconButton onClick={onEdit}>
                                <EditIcon />
                            </IconButton>
                        </Tooltip>

                        <Tooltip title="History" placement="top">
                            <IconButton onClick={onShowHistory}>
                                <AvTimerIcon />
                            </IconButton>
                        </Tooltip>
                    </Box>

                    <StyledTextArea
                        disabled={disabled}
                        id="descriptions"
                        onChange={function (e) {
                            formik.setFieldValue(
                                "descriptions",
                                e.currentTarget.value
                            );
                        }}
                        defaultValue={formik.values.descriptions}
                        error={!!formik.errors.descriptions}
                    />
                    <Box
                        pt={3}
                        display="flex"
                        flexDirection="row"
                        justifyContent="flex-end"
                        width="100%"
                        flexWrap="wrap"
                    >
                        {!disabled && (
                            <>
                                <Box>
                                    <LogStatusHandler
                                        id={id}
                                        onUpdateLogStatus={onUpdateStatus}
                                    />
                                </Box>
                            </>
                        )}
                    </Box>
                </Box>
            </Box>
        </>
    );
}

export default InReviewItems;
